import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { purple } from '@material-ui/core/colors';
import { ThemeProvider } from '@material-ui/styles';
import { makeStyles, createMuiTheme } from '@material-ui/core/styles';
import { Grid, TextField, InputAdornment, Select, MenuItem } from '@material-ui/core';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: purple['A200']
    }
  },
  typography: {
    fontFamily: 'CircularStd-Book'
  }
});

const TextfieldAndSelect = props => {
  const classes = useStyles(props);
  const {
    onChangeSelect,
    optionsSelect,
    selectValue,
    maxLength,
    className,
    required,
    disabled,
    onChange,
    tabIndex,
    label,
    value,
    type,
    id
  } = props;

  const [focusOut, setStateFocusOut] = useState(false);
  const [error, setError] = useState(false);

  useEffect(() => {
    const requiredFieldError = required && focusOut && !value;
    setError(requiredFieldError);
  }, [required, focusOut, value]);

  const onInput = (event) => {
    if (maxLength) {
      event.target.value = Math.max(0, parseInt(event.target.value)).toString().slice(0, maxLength);
    }
  }

  return (
    <Grid container>
      <Grid item xs={12} onFocus={() => setStateFocusOut(false)}>
        <ThemeProvider theme={theme}>
          <TextField
            onBlur={() => setStateFocusOut(true)}
            onInput={onInput}
            disabled={disabled}
            error={error}
            className={className}
            label={label}
            required={required}
            onChange={onChange}
            variant="outlined"
            value={value}
            type={type}
            min={0}
            id={id}
            InputProps={{
              inputProps: { tabIndex: tabIndex },
              endAdornment: (
                <InputAdornment position="end" >
                  <Select
                    id='selectValue'
                    className={classes.select}
                    disableUnderline
                    onBlur={() => setStateFocusOut(true)}
                    disabled={disabled}
                    value={selectValue}
                    onChange={onChangeSelect}
                    inputProps={{ tabIndex: tabIndex }}
                  >
                    {optionsSelect.map((option) => {
                      return (
                        <MenuItem key={option.codigo} value={option.codigo}>
                          {option.descricao}
                        </MenuItem>
                      )
                    })
                    }
                  </Select>
                </InputAdornment>
              ),
            }}
          />
        </ThemeProvider>
      </Grid>
    </Grid>
  );
}

const useStyles = makeStyles({
  select: {
    borderLeft: '0.05em solid #b5b5b5',
    paddingLeft: '0.8em'
  }
});

TextfieldAndSelect.propTypes = {
  onChangeSelect: PropTypes.func.isRequired,
  optionsSelect: PropTypes.array.isRequired,
  selectValue: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  maxLength: PropTypes.number,
  required: PropTypes.bool,
  disabled: PropTypes.bool,
  className: PropTypes.string,
  tabIndex: PropTypes.number,
  label: PropTypes.string,
  id: PropTypes.string,
  type: PropTypes.string
};

TextfieldAndSelect.defaultProps = {
  required: true,
  disabled: true,
  optionsSelect: {},
  maxLength: 0,
  className: '',
  tabIndex: 0,
  selectValue: '',
  label: '',
  value: 0,
  id: '',
  type: 'text'
};

export default TextfieldAndSelect;