import React, { useState, useEffect } from 'react';
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers';
import { makeStyles, createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import { Calendar } from 'react-feather';
import { Grid } from '@material-ui/core';
import PropTypes from 'prop-types';
import DateFnsUtils from '@date-io/date-fns';
import ptLocale from 'date-fns/locale/pt-BR';
import { withStyles } from '@material-ui/core/styles';

const theme = createMuiTheme({
  typography: {
    fontFamily: 'CircularStd-Book'
  },
  palette: {
    primary: {
      main: '#20174A'
    }
  }
});

const styles = () => ({
  notchedOutline: {},
  focused: {
    '& $notchedOutline': {
      borderColor: '#e040fb !important'
    }
  },
  floatingLabelFocusStyle: {
    color: '#9b2caf !important'
  }
});

export const Datepicker = props => {
  const {
    invalidDateMessage,
    maxDateMessage,
    minDateMessage,
    hideCalendar,
    inputVariant,
    cancelLabel,
    inputFocus,
    required,
    disabled,
    onChange,
    minDate,
    maxDate,
    variant,
    styles,
    format,
    autoOk,
    label,
    value,
    id
  } = props;

  const classes = useStyles(props);
  const [focusOut, setStateFocusOut] = useState(false);
  const [error, setError] = useState(false);

  const realMaxDate = maxDate;
  if (realMaxDate) realMaxDate.setSeconds(59);

  let realValue = value;
  if (realValue) {
    const dateSelected = new Date(realValue);
    realValue = new Date(
      dateSelected.getFullYear(),
      dateSelected.getMonth(),
      dateSelected.getDate()
    );
  }
  useEffect(() => {
    const requiredFieldError = required && focusOut && !realValue;
    const invalidDateError = isNaN(realValue);
    const outOfBoundaryDateError = realValue < minDate || realValue > realMaxDate;

    setError(
      requiredFieldError ||
        (realValue && (invalidDateError || outOfBoundaryDateError))
    );
  }, [required, realValue, focusOut, minDate, realMaxDate]);

  const focus = input => {
    if (input) {
      input.focus();
    }
  };

  const inputProperties = inputFocus
    ? {
        variant: 'outlined',
        inputRef: focus,
        classes: { notchedOutline: classes.notchedOutline, focused: classes.focused }
      }
    : {
        variant: 'outlined',
        classes: { notchedOutline: classes.notchedOutline, focused: classes.focused }
      };

  const InputLabelProps = {
    classes: { focused: classes.floatingLabelFocusStyle }
  };

  const inputAdornmentProps = hideCalendar
    ? { classes: { root: classes.hideIcon } }
    : { onBlur: () => setStateFocusOut(true) };

  return (
    <Grid container>
      <Grid item xs={12} onFocus={() => setStateFocusOut(false)}>
        <ThemeProvider theme={theme}>
          <MuiPickersUtilsProvider utils={DateFnsUtils} locale={ptLocale}>
            <KeyboardDatePicker
              keyboardIcon={<Calendar color='#61688B' size={24} />}
              InputAdornmentProps={inputAdornmentProps}
              invalidDateMessage={invalidDateMessage}
              InputLabelProps={InputLabelProps}
              onBlur={() => setStateFocusOut(true)}
              maxDateMessage={maxDateMessage}
              minDateMessage={minDateMessage}
              InputProps={inputProperties}
              inputVariant={inputVariant}
              className={classes.input}
              error={error}
              required={required}
              disabled={disabled}
              maxDate={maxDate}
              minDate={minDate}
              cancelLabel={cancelLabel}
              value={realValue}
              onChange={onChange}
              variant={variant}
              label={label}
              format={format}
              style={styles}
              autoOk={autoOk}
              id={id}
            />
          </MuiPickersUtilsProvider>
        </ThemeProvider>
      </Grid>
    </Grid>
  );
};

const useStyles = makeStyles({
  hideIcon: {
    display: 'none'
  },
  input: {
    display: 'flex',
    width: '100%'
  }
});

Datepicker.propTypes = {
  invalidDateMessage: PropTypes.string,
  maxDateMessage: PropTypes.string,
  minDateMessage: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  hideCalendar: PropTypes.bool,
  required: PropTypes.bool,
  disabled: PropTypes.bool,
  inputVariant: PropTypes.string,
  cancelLabel: PropTypes.string,
  label: PropTypes.string,
  inputFocus: PropTypes.bool,
  variant: PropTypes.string,
  minDate: PropTypes.any,
  maxDate: PropTypes.any,
  styles: PropTypes.object,
  format: PropTypes.string,
  autoOk: PropTypes.bool,
  value: PropTypes.any,
  id: PropTypes.string
};

Datepicker.defaultProps = {
  maxDateMessage: 'A data inserida no pode estar no futuro',
  minDate: new Date(new Date().getFullYear() - 130, 0, 1),
  minDateMessage: 'A data inserida  muito antiga',
  invalidDateMessage: 'Data invlida',
  proriedadesPersonalizadas: {},
  inputVariant: 'outlined',
  hideCalendar: false,
  maxDate: new Date(),
  cancelLabel: 'Cancelar',
  required: false,
  disabled: false,
  format: 'dd/MM/yyyy',
  variant: 'inline',
  inputFocus: false,
  label: '',
  autoOk: true,
  styles: {},
  value: null
};

export default withStyles(styles)(Datepicker);
