import React, { useState, useEffect } from "react";
import { purple } from "@material-ui/core/colors";
import { makeStyles } from "@material-ui/core/styles";
import ReactSelect from "react-select";
import { FormControl, InputLabel } from "@material-ui/core";
import PropTypes from "prop-types";

const Select = props => {
  const {
    customProperties,
    returnFullObject,
    noOptionsMessage,
    openMenuOnFocus,
    getOptionValue,
    getOptionLabel,
    maxHeightMenu,
    filterOption,
    handleChange,
    isClearable,
    autoFocus,
    disabled,
    required,
    options,
    label,
    code,
    id
  } = props;

  const classes = useStyles();
  const value = code ? options.find(o => o.codigo === code) : null;
  const realLabel = required ? label + "*" : label;

  const [selectedField, setSelectedField] = useState(null);
  const [errorBehavior, setErrorBehavior] = useState(false);

  useEffect(() => {
    setErrorBehavior(
      !selectedField && selectedField !== null && !value && required
    );
  }, [selectedField, value, required]);

  const handleSelectOption = option => {
    handleChange(returnFullObject ? option : option.codigo);
  };

  return (
    <FormControl
      id="form"
      variant="outlined"
      className={classes.input}
      onClick={() => setSelectedField(true)}
    >
      {code && (
        <InputLabel
          style={{
            color:
              errorBehavior && !disabled
                ? "#f44336"
                : selectedField && !disabled
                ? purple["A200"]
                : "rgba(0, 0, 0, 0.54)",
            backgroundImage: disabled
              ? "linear-gradient(180deg, transparent 50%, hsl(0,0%,95%) 50%)"
              : "none"
          }}
          className={classes.floatLabel}
          htmlFor="outlined-age-native-simple"
        >
          {realLabel}
        </InputLabel>
      )}
      <ReactSelect
        noOptionsMessage={() => noOptionsMessage}
        onBlur={() => setSelectedField(false)}
        styles={styles(errorBehavior, maxHeightMenu)}
        getOptionValue={getOptionValue}
        getOptionLabel={getOptionLabel}
        {...customProperties}
        onChange={handleSelectOption}
        placeholder={realLabel}
        filterOption={filterOption}
        isDisabled={disabled}
        isClearable={isClearable}
        className={classes.input}
        autoFocus={autoFocus}
        options={options}
        openMenuOnFocus={openMenuOnFocus}
        tabSelectsValue
        value={value}
        id={id}
      />
    </FormControl>
  );
};

const styles = (errorBehavior, maxHeight) => ({
  menuList: styles => ({ ...styles, maxHeight: maxHeight }),
  menu: styles => ({ ...styles, zIndex: 2 }),
  input: provided => ({
    ...provided,
    paddingTop: "12px",
    paddingBottom: "12px",
    width: "100%",
    fontFamily: "CircularStd-Book",
    fontSize: "16px"
  }),
  singleValue: provided => ({
    ...provided,
    fontSize: "16px"
  }),
  placeholder: provided => ({
    ...provided,
    fontFamily: "CircularStd-Book",
    fontSize: "1rem",
    color: errorBehavior ? "#f44336" : "rgba(0, 0, 0, 0.54)"
  }),
  control: (styles, { isFocused }) => ({
    ...styles,
    ":hover": {
      borderColor: errorBehavior
        ? "#f44336"
        : isFocused
        ? purple["A200"]
        : "black"
    },
    boxShadow: isFocused ? `0 0 0 1px ${purple["A200"]}` : "none",
    borderColor: errorBehavior
      ? "#f44336"
      : isFocused
      ? purple["A200"]
      : "hsl(0, 0%, 70%)"
  }),
  option: (styles, { isDisabled, isFocused, isSelected }) => {
    return {
      ...styles,
      backgroundColor: isDisabled
        ? null
        : isSelected || isFocused
        ? "hsl(0,0%,95%)"
        : null,
      cursor: "pointer",
      color: "rgba(0,0,0,0.54)",
      ":active": {
        ...styles[":active"],
        backgroundColor: "transparent"
      }
    };
  }
});

const useStyles = makeStyles({
  input: {
    width: "100%",
    maxHeight: "100px"
  },
  disabled: {
    backgroundColor: "rgb(242, 242, 242)",
    borderColor: "rgb(230, 230, 230)"
  },
  floatLabel: {
    backgroundColor: "white",
    transform: "scale(0.75)",
    padding: "0 8px 0 8px",
    position: "absolute",
    fontSize: "1rem",
    lineHeight: "1",
    height: "16px",
    top: "-6px",
    left: "6px"
  }
});

Select.propTypes = {
  /** Cdigo da opo selecionada */
  code: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  /** Propriedades do componente ReactSelect que no esto mapeadas nas props, podem ser adicionadas nesse objeto */
  customProperties: PropTypes.object,
  handleChange: PropTypes.func.isRequired,
  /** Se essa prop for true, a function handleChange receber como parmetro o objeto completo da opo */
  returnFullObject: PropTypes.bool,
  noOptionsMessage: PropTypes.string,
  maxHeightMenu: PropTypes.string,
  required: PropTypes.bool,
  disabled: PropTypes.bool,
  isClearable: PropTypes.bool,
  label: PropTypes.string,
  autoFocus: PropTypes.bool,
  /** Espera-se um array de objetos com as propriedades 'codigo' e 'descricao' */
  options: PropTypes.array,
  id: PropTypes.string,
  openMenuOnFocus: PropTypes.bool,
  /** Function para pesquisa de opes */
  filterOption: PropTypes.func,
  getOptionValue: PropTypes.func,
  getOptionLabel: PropTypes.func
};

Select.defaultProps = {
  noOptionsMessage: "Sem opes",
  returnFullObject: false,
  customProperties: {},
  required: false,
  maxHeightMenu: "300px",
  disabled: false,
  isClearable: false,
  autoFocus: false,
  label: "",
  code: null,
  options: [],
  id: "",
  openMenuOnFocus: true,
  getOptionValue: option => option.codigo,
  getOptionLabel: option => option.descricao,
  filterOption: (option, text) =>
    option.label
      .toString()
      .toUpperCase()
      .startsWith(text.toUpperCase())
};

export default Select;
