import React, { useState, useEffect } from "react";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker
} from "@material-ui/pickers";
import { makeStyles, createMuiTheme } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/styles";
import DateFnsUtils from "@date-io/date-fns";
import { Clock } from "react-feather";
import { Grid } from "@material-ui/core";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import ptLocale from "date-fns/locale/pt-BR";

import { timeValidation } from "../../utils/validations";

const theme = createMuiTheme({
  typography: {
    fontFamily: "CircularStd-Book"
  },
  palette: {
    primary: {
      main: "#20174A"
    }
  }
});

const styles = () => ({
  notchedOutline: {},
  focused: {
    "& $notchedOutline": {
      borderColor: "#e040fb !important"
    }
  },
  floatingLabelFocusStyle: {
    color: "#9b2caf !important"
  }
});

export const Timepicker = props => {
  const {
    KeyboardButtonProps,
    invalidDateMessage,
    customProperties,
    referenceDay,
    inputVariant,
    cancelLabel,
    required,
    disabled,
    onChange,
    variant,
    styles,
    autoOk,
    label,
    value,
    ampm,
    id
  } = props;

  const classes = useStyles(props);
  const [focusOut, setStateFocusOut] = useState(false);
  const [error, setError] = useState(false);

  useEffect(() => {
    const requiredFieldError = required && focusOut && !value;
    const invalidTimeError = isNaN(value);
    setError(requiredFieldError || (value && invalidTimeError));
  }, [required, focusOut, value]);

  const inputProperties = {
    variant: "outlined",
    error: (value && !timeValidation(value, referenceDay)) || error,
    classes: {
      notchedOutline: classes.notchedOutline,
      focused: classes.focused
    }
  };

  const inputLabelProperties = {
    error: (value && !timeValidation(value, referenceDay)) || error,
    classes: { focused: classes.floatingLabelFocusStyle }
  };

  const inputAdornmentProps = { onBlur: () => setStateFocusOut(true) };

  return (
    <Grid container>
      <Grid item xs={12} onFocus={() => setStateFocusOut(false)}>
        <ThemeProvider theme={theme}>
          <MuiPickersUtilsProvider utils={DateFnsUtils} locale={ptLocale}>
            <KeyboardTimePicker
              {...customProperties}
              keyboardIcon={<Clock color="#61688B" size={24} />}
              KeyboardButtonProps={KeyboardButtonProps}
              InputLabelProps={inputLabelProperties}
              InputAdornmentProps={inputAdornmentProps}
              invalidDateMessage={invalidDateMessage}
              onBlur={() => setStateFocusOut(true)}
              InputProps={inputProperties}
              className={classes.input}
              inputVariant={inputVariant}
              required={required}
              disabled={disabled}
              cancelLabel={cancelLabel}
              onChange={onChange}
              label={label}
              variant={variant}
              style={styles}
              autoOk={autoOk}
              value={value}
              ampm={ampm}
              id={id}
            />
          </MuiPickersUtilsProvider>
        </ThemeProvider>
      </Grid>
    </Grid>
  );
};

const useStyles = makeStyles({
  input: {
    display: "flex",
    width: "100%"
  }
});

Timepicker.propTypes = {
  customProperties: PropTypes.object,
  invalidDateMessage: PropTypes.string,
  KeyboardButtonProps: PropTypes.object,
  onChange: PropTypes.func.isRequired,
  required: PropTypes.bool,
  cancelLabel: PropTypes.string,
  disabled: PropTypes.bool,
  inputVariant: PropTypes.string,
  referenceDay: PropTypes.any,
  label: PropTypes.string,
  variant: PropTypes.string,
  styles: PropTypes.object,
  autoOk: PropTypes.bool,
  value: PropTypes.any,
  ampm: PropTypes.bool,
  id: PropTypes.string
};

Timepicker.defaultProps = {
  invalidDateMessage: "Hora invlida",
  customProperties: {},
  inputVariant: "outlined",
  KeyboardButtonProps: {
    "aria-label": "change time"
  },
  required: false,
  cancelLabel: "Cancelar",
  disabled: false,
  referenceDay: null,
  variant: "inline",
  label: "",
  autoOk: true,
  styles: {},
  value: null,
  ampm: false
};

export default withStyles(styles)(Timepicker);
