import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { purple } from '@material-ui/core/colors';
import { Grid, TextField } from '@material-ui/core';
import { makeStyles, createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: purple['A200']
    }
  },
  typography: {
    fontFamily: 'CircularStd-Book'
  }
});

const Textfield = props => {
  const {
    activeErrorBehavior,
    customProperties,
    numberOfLines,
    maxCharacters,
    errorMessage,
    placeholder,
    required,
    onChange,
    styles,
    label,
    value,
    mask,
    id
  } = props;
  const classes = useStyles(props);
  const [focusOut, setStateFocus] = useState(false);

  const inputProperties = mask
    ? { InputProps: { inputComponent: mask } }
    : { inputProps: { maxLength: maxCharacters } };

  const realValue = value || '';

  return (
    <Grid container>
      <Grid item xs={12}>
        <ThemeProvider theme={theme}>
          <TextField
            {...customProperties}
            {...inputProperties}
            error={activeErrorBehavior || (required && focusOut && realValue.length === 0)}
            helperText={errorMessage && errorMessage.length > 0 ? errorMessage : null}
            onBlur={() => setStateFocus(true)}
            multiline={numberOfLines > 1}
            className={classes.textfield}
            placeholder={placeholder}
            required={required}
            rows={numberOfLines}
            value={realValue}
            onChange={onChange}
            variant='outlined'
            label={label}
            style={styles}
            id={id}
          />
        </ThemeProvider>
      </Grid>
    </Grid>
  );
};

const useStyles = makeStyles({
  textfield: {
    width: '100%'
  }
});

Textfield.propTypes = {
  /** Propriedades do componente TextField do MaterialUI que no esto mapeadas nas props, podem ser adicionadas nesse objeto */
  customProperties: PropTypes.object,
  /** Prop para indicar se valor falhou em alguma validao */
  activeErrorBehavior: PropTypes.bool,
  maxCharacters: PropTypes.number,
  numberOfLines: PropTypes.number,
  required: PropTypes.bool,
  errorMessage: PropTypes.string,
  label: PropTypes.string,
  /** Estilos adicionais */
  styles: PropTypes.object,
  onChange: PropTypes.func,
  mask: PropTypes.func,
  value: PropTypes.string,
  id: PropTypes.string,
  placeholder: PropTypes.string
};

Textfield.defaultProps = {
  customProperties: {},
  activeErrorBehavior: false,
  maxCharacters: null,
  required: false,
  numberOfLines: 1,
  errorMessage: '',
  onChange: null,
  mask: null,
  label: '',
  styles: {},
  value: '',
  id: ''
};

export default Textfield;