import React, { useState, useEffect } from 'react';
import AsyncSelect from 'react-select/async';
import { makeStyles } from '@material-ui/core/styles';
import { components } from 'react-select';
import { InputLabel } from '@material-ui/core';
import { purple } from '@material-ui/core/colors';
import PropTypes from 'prop-types';

const AutoComplete = props => {
  const {
    noOptionsMessage,
    getOptionLabel,
    getOptionValue,
    loadingMessage,
    defaultOptions,
    menuPlacement,
    onInputChange,
    invalidField,
    placeholder,
    addonOption,
    cacheOpcoes,
    loadOptions,
    isClearable,
    inputValue,
    autoFocus,
    required,
    disabled,
    onChange,
    onBlur,
    value,
    theme,
    id
  } = props;

  const [error, setError] = useState(false);
  const [focus, setFocus] = useState(false);

  const onblurInterno = () => {
    onBlur && onBlur();
    if(required && !(value || inputValue)) {
      setError(true);
    } else {
      setError(false);
    }
    setFocus(false);
  }

  useEffect(() => {
    if(required && (value || inputValue)) {
      setError(false);
    } 
  }, [required, value, inputValue]);

  const onFocus = () => {
    setFocus(true);
    setError(false);
  }

  const classes = useStyles();

  const placeholderTratado = required ? `${placeholder}*` : placeholder;
  const option = properties => {
    return (
      <div className={classes.divOption}>
        <components.Option {...properties} />
        {addonOption && addonOption(properties.data, properties.isFocused)}
      </div>
    );
  };

  return (
    <React.Fragment>
      {(value || inputValue) && (
        <InputLabel 
          style={{
            color: (error || invalidField) ? '#f44336' : focus ? purple['A200'] : 'rgba(0, 0, 0, 0.54)',
            backgroundImage: disabled ? 'linear-gradient(180deg, transparent 50%, hsl(0,0%,95%) 50%)' : 'none'
          }}
          className={classes.floatLabel} 
          htmlFor='outlined-age-native-simple'
        >
          {placeholderTratado}
        </InputLabel>
      )}
      <AsyncSelect
        id={id}
        autoFocus={autoFocus}
        placeholder={placeholderTratado}
        noOptionsMessage={noOptionsMessage}
        openMenuOnClick={false}
        closeMenuOnSelect={true}
        blurInputOnSelect={false}
        menuPlacement={menuPlacement}
        defaultOptions={defaultOptions}
        isClearable={isClearable}
        loadOptions={loadOptions}
        getOptionValue={getOptionValue}
        getOptionLabel={getOptionLabel}
        styles={styles(error, invalidField)}
        loadingMessage={loadingMessage}
        onChange={onChange}
        theme={theme}
        value={value}
        components={{ Option: option }}
        tabSelectsValue={false}
        onInputChange={onInputChange}
        onBlur={onblurInterno}
        inputValue={inputValue}
        cacheOptions={cacheOpcoes}
        onFocus={onFocus}
        isDisabled={disabled}
      />
    </React.Fragment>
  );
};

const styles = (error, invalidField) => ({
  menu: styles => ({ ...styles, zIndex: 999 }),
  input: provided => ({
    ...provided,
    paddingTop: '12px',
    paddingBottom: '12px',
    width: '100%',
    fontFamily: 'CircularStd-Book',
    fontSize: '16px',
    input: {
      opacity: '1 !important'
    }
  }),
  control: (styles, { isFocused }) => {
    const fieldWithError = invalidField || (error && !isFocused);
    return {
      ...styles,
      ':hover': {
        borderColor:  fieldWithError? '#f44336' : isFocused ? purple['A200'] : 'black'
      },
      boxShadow: isFocused && !invalidField ? `0 0 0 1px ${purple['A200']}` : 'none',
      borderColor: fieldWithError ? '#f44336' : isFocused ? purple['A200'] : 'hsl(0, 0%, 70%)'
  }},
  placeholder: (styles, { isFocused }) => ({
    ...styles,
    fontFamily: 'CircularStd-Book',
    fontSize: '1rem',
    color: error && !isFocused ? '#f44336' : 'rgba(0, 0, 0, 0.54)'
  }),
  option: (styles, { isDisabled, isFocused, isSelected }) => {
    return {
      ...styles,
      backgroundColor: isDisabled ? null : isSelected || isFocused ? 'hsl(0,0%,95%)' : null,
      cursor: 'pointer',
      color: 'rgba(0,0,0,0.54)',
      ':active': {
        ...styles[':active'],
        backgroundColor: 'transparent'
      }
    };
  }
});

const useStyles = makeStyles({
  floatLabel: {
    color: 'rgba(0, 0, 0, 0.54)',
    padding: '0 8px 0 8px',
    zIndex: '999',
    position: 'absolute',
    fontSize: '1rem',
    transform: 'scale(0.75) translate(8px, -8px)',
    lineHeight: '1',
    backgroundColor: 'white'
  },
  divOption: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between'
  }
});

AutoComplete.propTypes = {
  id:PropTypes.string,
  autoFocus:PropTypes.bool,
  placeholder:PropTypes.string,
  noOptionsMessage:PropTypes.func,
  loadingMessage:PropTypes.func,
  menuPlacement:PropTypes.string,
  defaultOptions:PropTypes.oneOfType([PropTypes.array, PropTypes.bool]),
  isClearable:PropTypes.bool,
  loadOptions:PropTypes.func,
  getOptionValue:PropTypes.func,
  getOptionLabel:PropTypes.func,
  onChange:PropTypes.func,
  theme:PropTypes.object,
  components:PropTypes.object,
  onInputChange:PropTypes.func,
  onBlur:PropTypes.func,
  inputValue:PropTypes.string,
  addonOption:PropTypes.func
};

AutoComplete.defaultProps = {
  id: '',
  autoFocus: false,
  noOptionsMessage: () => 'No foram encontrados registros',
  loadingMessage: () => 'Carregando...',
  menuPlacement: 'auto',
  isClearable: false
};

export default AutoComplete;